.header {
    margin: 0;
    padding: 0;
    background-color: whitesmoke;
    width: 100%;
    padding: 30px 4% 10px;
    align-items: center;
    display: flex;
}

.shion {
    margin: 0;
    padding: 0;
    font-size: 35px;

}

a {
    text-decoration: none;
    color: #4b4b4b;

}

